* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
h1 {
  margin: 0;
}
body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  min-height: 100vh;
  font-size: 16px;
  position: relative;
  font-family: 'Montserrat', sans-serif;
  transition: 0.3s;
  display: flex;
  background-color: #f5f5f5;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 1170px) and (-webkit-min-device-pixel-ratio: 1.2) {
  body {
    font-size: 14px;
  }
}
.container {
  padding: 0 10px;
  margin: 0 auto;
  width: 1170px;
  box-sizing: border-box;
}
@media (min-width: 320px) {
  .container {
    width: 90%;
    min-width: 320px;
  }
}
@media (min-width: 740px) {
  .container {
    width: 740px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 992px;
  }
}
@media (min-width: 1170px) {
  .container {
    width: 1170px;
  }
}
.page-header {
  padding: 30px 0;
}
.page-header__inner {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.logo__title {
  font-size: 1em;
  color: #1794AB;
  display: inline-block;
  text-align: left;
  text-decoration: none;
  position: relative;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.logo__text {
  font-size: 0.9em;
  color: #212121;
  display: inline-block;
  text-align: left;
  text-decoration: none;
  position: relative;
  text-transform: uppercase;
  font-weight: 300;
  display: block;
}
.main {
  margin-top: 40px;
  margin-bottom: 60px;
}
.main__inner {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: nowrap;
}
.text {
  flex-basis: 75%;
  text-align: center;
}
.text h1 {
  font-size: 1em;
  color: #212121;
  display: inline-block;
  text-decoration: none;
  position: relative;
  font-weight: 700;
  margin-bottom: 15px;
  text-align: left;
}
.text p {
  font-size: 0.8em;
  color: #212121;
  display: inline-block;
  text-decoration: none;
  position: relative;
  text-indent: 30px;
  margin-bottom: 15px;
  text-align: left;
  display: block;
  line-height: 16px;
}
.text ul {
  margin-bottom: 15px;
  text-align: left;
}
.text li {
  font-size: 0.8em;
  color: #212121;
  display: inline-block;
  text-align: left;
  text-decoration: none;
  position: relative;
  font-weight: 700;
  text-indent: 30px;
}
.text__wrapper {
  padding-right: 15px;
}
.text__button {
  display: inline-block;
  text-decoration: none;
  width: 150px;
  height: 25px;
  font-size: 0.8em;
  color: #f5f5f5;
  background-color: #1794AB;
  line-height: 24px;
  text-align: center;
  transition: 0.3s;
  cursor: pointer;
  border: 0;
  margin-top: 40px;
}
.text__button:hover {
  transition: 0.3s;
  background-color: #808080;
}
.text__button:active {
  transition: 0.3s;
  background-color: #21C2DE;
}
.sidebar {
  flex-basis: 25%;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.sidebar__col {
  padding-left: 15px;
  flex-basis: 50%;
  width: 375px;
  border-left: 2px solid #21C2DE;
}
.sidebar__col:first-of-type {
  text-align: right;
  padding-left: 0;
  padding-right: 15px;
  flex-basis: 50%;
  border: 0;
  width: 240px;
  display: none;
}
.sidebar__item {
  list-style-type: none;
}
.sidebar__link {
  font-size: 0.8em;
  color: #212121;
  display: inline-block;
  text-align: left;
  text-decoration: none;
  position: relative;
  white-space: nowrap;
  line-height: 26px;
}
.sidebar__link:hover {
  color: #21C2DE;
  text-decoration: underline;
}
.page-footer {
  margin-top: auto;
  padding: 15px 0;
}
.page-footer__text {
  font-size: 0.8em;
  color: #212121;
  display: inline-block;
  text-align: left;
  text-decoration: none;
  position: relative;
}
.city {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.city__name {
  font-size: 0.8em;
  color: #21C2DE;
  display: inline-block;
  text-align: left;
  text-decoration: none;
  position: relative;
  margin-right: 50px;
}
.city__select {
  background-color: transparent;
  border: 0;
  font-size: 0.8em;
  color: #212121;
  display: inline-block;
  text-align: left;
  text-decoration: none;
  position: relative;
}
