// Для импорта в диспетчер подключений: @import './src/less/blocks/logo.less';


.logo {
  &__title {
    .text(@deep-blue-color, 1em);
    font-weight: 700;
    text-transform: uppercase;
    display: block;
  }
  &__text {
    .text(@black-color, 0.9em);
    text-transform: uppercase;
    font-weight: 300;
    display: block;
  }
}
