.text(@color, @size) {
  font-size: @size;
  color: @color;
  display: inline-block;
  text-align: left;
  text-decoration: none;
  position: relative;
}

.btn(@width, @base, @hover, @active) {
  display: inline-block;
  text-decoration: none;
  width: @width;
  height: 25px;
  font-size: 0.8em;
  color: @fake-white-color;
  background-color: @base;
  line-height: 24px;
  text-align: center;
  transition: 0.3s;
  cursor: pointer;
  border: 0;

  &:hover {
    transition: 0.3s;
    background-color: @hover;
  }
  &:active {
    transition: 0.3s;
    background-color: @active;
  }
}

.input() {
  display: inline-block;
  width: 100%;
  border: 0;
  outline: none;
  margin: 10px;
  height: 30px;
  line-height: 28px;
  font-size: 0.8em;
  background-color: @light-gray-color;
  padding-left: 5px;
}
