// Для импорта в диспетчер подключений: @import './src/less/blocks/city.less';


.city {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  align-items: center;

  &__name {
    .text(@blue-color, 0.8em);
    margin-right: 50px;
  }

  &__select {
    background-color: transparent;
    border: 0;
    .text(@black-color, 0.8em);
  }
}
