// Для импорта в диспетчер подключений: @import './src/less/blocks/sidebar.less';


.sidebar {
  flex-basis: 25%;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  align-items: flex-start;

  &__col {
    padding-left: 15px;
    flex-basis: 50%;
    width: 375px;
    border-left: 2px solid @blue-color;
  }
  &__col:first-of-type {
    text-align: right;
    padding-left: 0;
    padding-right: 15px;
    flex-basis: 50%;
    border: 0;
    width: 240px;
    display: none;
  }
  &__list {

  }
  &__item {
    list-style-type: none;
  }
  &__link {
    .text(@black-color, 0.8em);
    white-space: nowrap;
    line-height: 26px;

    &:hover {
      color: @blue-color;
      text-decoration: underline;
    }
  }

}
