// Для импорта в диспетчер подключений: @import './src/less/blocks/text.less';


.text {
  flex-basis: 75%;
  text-align: center;

  h1 {
    .text(@black-color, 1em);
    font-weight: 700;
    margin-bottom: 15px;
    text-align: left;
  }
  p {
    .text(@black-color, 0.8em);
    text-indent: 30px;
    margin-bottom: 15px;
    text-align: left;
    display: block;
    line-height: 16px;
  }
  ul {
    margin-bottom: 15px;
    text-align: left;
  }
  li {
    .text(@black-color, 0.8em);
    font-weight: 700;
    text-indent: 30px;
  }
  &__wrapper {
    padding-right: 15px;
  }
  &__button {
    .btn(150px, @deep-blue-color, @deep-gray-color, @blue-color);
    margin-top: 40px;
  }

}
